$home: '.homepage';

#{$home}
{   
    .recent-posts{
        background-image: url(../img/research/ifs-watermark.jpg);
        background-repeat: no-repeat;
        background-position: right 0% top;
        background-size: auto;
        .destacada{
          padding-top:5rem;
          // .articles{
          //   padding-bottom: 0 !important;
          // }
        }

        .articles{
            img{
                max-width: 180px;    
            }

            &:last-child{
                .articles__item:after{
                    background: none;
                }
            }
        }
    }
    .featured-case{
        background-color: #efefef;

        .articles{
            padding-bottom: 0;
            background-image: none;


            
            .featured-case__text{
                margin: 3.5rem 0rem;

                .articles__more{
                   display: block;
                   width: 7rem;
                   background-color:#757575;
                   color: #ffffff;  
                   text-align: center;   
                   margin-left: 0; 
                   margin-bottom: 2.5rem;
                }

                .btn--blue{
                   width: 14rem;
                   text-align: center;
                   display: inline-block; 
                   margin: 0.5rem;

                   @include breakpoint(medium down){
                    width: 100%;
                    margin: 0.5rem auto;
                   }
                }
            }

        }

        img {
            width: 100%;
        }

        .candle{
            width: auto;
            position: absolute;
            top: -3.5rem; 
            right: 0;
        }
    }

    .articles{
        padding-bottom: 0 !important;
        background-image: none;
    }
}